.PassOut {
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  color: #fff; }
  .PassOut .wake-up {
    position: relative;
    width: 80%;
    margin: 0 auto;
    text-align: center; }
    .PassOut .wake-up-text::after {
      content: '"You lost you must pay the bill"';
      -webkit-animation: 30s ease changeTextWithFade;
              animation: 30s ease changeTextWithFade; }

@-webkit-keyframes changeTextWithFade {
  0% {
    opacity: 1;
    content: 'You wake up'; }
  10% {
    opacity: 0; }
  14% {
    content: 'You look at your phone'; }
  15% {
    opacity: 1; }
  30% {
    opacity: 0; }
  49% {
    content: 'You have a text from david'; }
  50% {
    opacity: 1; }
  60% {
    opacity: 0; }
  100% {
    content: '"You lost you must pay the bill"';
    opacity: 1; } }

@keyframes changeTextWithFade {
